.logo{
    padding: 40px 0 0 30px;
    margin-bottom: 20px;
    color: white;
    font-size: $lg-size;
}

.nav{
    display: flex;
    color: white;
    width: 100%;
    max-height: 60px;
    font-size: $lg-size;
    align-items: center;
    &__home{
        margin-left: 80px;
    }
    &__services{
        margin-left: 40px;
    }
    &__about{
        margin-left: 40px;
        flex-grow: 1;
    }
    &__button{
        margin-right: 80px;
        // background: #6BA5AD;
        background: #28DDEA;
        color: #352929;
        border-radius: 15px;
        padding: 5px 10px;
    }
    &__button:hover{
        // background: #28DDEA;
        background: #6BA5AD;
        color: #ffff;
        cursor: grab;
    }
}
