.hero{
    display: flex;
    margin: 100px 0;
    color: $primary-color; 
    &__container{
        max-width: 520px;
        margin-right: 70px;
        padding: 20px;
        &-title{
            font-size: $lg-size;
            font-weight: 800;
            margin-bottom: 40px;
        }
        &-article{
            font-size: $md-size;
        }
        &-btn{
            font-size: $lg-size;
            margin: 50px 0 0 150px;
            background: #28DDEA;
            color: #352929;
            border-radius: 15px;
            padding: 7px 25px;
        }
        &-btn:hover{
            background: #6BA5AD;
            color: #ffff;
            cursor: grab;
        }
    }
    &__img{
        background-image: url("../assets/hero_img.png");
        background-repeat: no-repeat;
        background-size: contain;
        width: 750px;
        height: 500px;
    }
}