.about{
    flex-direction: column;
    color: $primary-color;
    margin-bottom: 80px;
    padding: 20px;
    &__title{
        @include h1;
    }
    &__article{
        @include article;
    }
    &__container{
        display: flex;
        &-img{
            background-image: url("../assets/about_img.png");
            background-repeat: no-repeat;
            background-size: contain;
            width: 750px;
            height: 500px;
            flex-grow: 1;
        }
    }
}

.list{
    &__title{
        font-size: 32px;
        text-align: center;
        margin: 40px 0;
    }
    &__aims{
        display: flex;
        width: 500px;
        align-items: center;
        margin-bottom: 40px;
    }
    &__aim{
        font-size: $sm-size;
        margin-left: 20px;
    }
}