.offers{
    color: $primary-color;
    padding: 20px;
    &__title{
        @include h1;
    }
    &__article{
        @include article;
        text-align: center;
    }
    &__container{
        display: flex;
        justify-content: center;
        &-left{
            &-list{
                display: flex;
                justify-content: flex-end;
                align-items: center;
                margin-bottom: 40px;
                padding-right: 60px;
            }
            &-svg{
                padding: 0 20px;
            }
        }
        &-right{
            &-list{
                display: flex;
                align-items: center;
                margin-bottom: 30px;
                padding-left: 60px;
            }
            &-svg{
                padding: 0 20px;
            }
        }
    }
}