.feature{
    color: $primary-color;
    padding: 20px;
    &__title{
        @include h1;
    }
    &__article{
        @include article;
    }
    &__container{
        &-title{
            @include article
        }
        &-lists{
            display: flex;
            align-items: center;
            margin: 0 0 40px 50px;
        }
        &-list{
            font-size: $sm-size;
            margin-left: 20px;
        }
    }
}