.footer{
    color: $primary-color;
    background: #352929;
    &__header{
        display: flex;
        justify-content: space-between;
        padding: 40px 80px 10px 80px;
    }
    &__container{
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    &__bottom{
        display: flex;
        padding: 20px;
        align-items: center;
    }
    &__logo{
        font-size: 30px;
        padding: 0 30px;
        margin-right: 20px;
    }
    &__cards{
        flex-grow: 1;
    }
    &__card{
        margin-right: 10px;
    }
}

.header__container-link{
    margin-bottom: 15px;
}

.header__container-title{
    font-size: $xl-size;
    margin-bottom: 15px;
}

.custom-btn:hover{
    background-color: #28DDEA;
    color: #352929;
    cursor: grab;
}

.custom-btn{
    display: flex;
    background: #DADADA;
    border-radius: 10px;
    padding: 5px 10px;
    font-size: 20px;
    color: #3C3D3C;
    align-items: center;
}

.custom-btn-text{
    margin-left: 10px;
}


.social{
    margin-top: 10px;
}

.social__icon{
    margin: 5px;
}
